.slider {
  cursor: pointer;
  background-image: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  transition: all .25s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  cursor: pointer;
  height: 18px;
  width: 18px;
  background-image: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .25s;
  position: absolute;
  bottom: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.switch:checked + .slider {
  background-image: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.switch:checked + .slider:before {
  transform: translateX(20px);
}

