.scroll-up {
  height: 54px;
  width: 54px;
  outline-offset: -1px;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  background-color: #4f2ee8;
  border-radius: 50%;
  outline: 5px solid rgba(80, 46, 232, .3);
  justify-content: center;
  align-items: center;
  transition: outline .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  animation: outlineInfin 3.5s linear infinite;
  display: flex;
  position: fixed;
  bottom: 110px;
  right: 50px;
  transform: scale(1);
}

.scroll-up > svg {
  width: 19px;
  height: 11px;
  fill: #fff;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
}

@keyframes outlineInfin {
  0% {
    outline: 5px solid rgba(80, 46, 232, .3);
  }

  50% {
    outline: 5px solid #502ee8;
  }

  100% {
    outline: 5px solid rgba(80, 46, 232, .3);
  }
}

.scroll-up:hover, .scroll-up:focus {
  animation-play-state: paused;
  transform: scale(1.1);
}

.scroll-up:hover > svg, .scroll-up:focus > svg {
  fill: #eac645;
  transform: scale(1.2);
}

@media screen and (min-width: 428px) and (max-width: 576px) {
  .scroll-up {
    bottom: 110px;
    right: 50px;
  }
}

@media screen and (min-width: 577px) and (max-width: 891px) {
  .scroll-up {
    bottom: 120px;
    right: 55px;
  }
}

@media screen and (min-width: 892px) {
